*,
*::before,
*::after {
  box-sizing: inherit;
}

html {
  font-family: "Roboto", "Arial", sans-serif;
  box-sizing: border-box;
}

body {
  margin: 0;
  padding-top: 100px;
}

.content-wrapper {
  max-width: 1450px;
  padding: 50px 80px;
  margin-right: auto;
  margin-left: auto;
}

.table {
  width: 100%;
  border-collapse: collapse;
  margin-bottom: 100px;

  thead {
    background-color: #4a238f; 
    color: white; 
    font-weight: bold;

    
  }

  td {
    padding: 15px;
  }

  .td__sub {
    padding-left: 40px;
  }
  
  .td__bordered {
    border-bottom: 2px solid gray;
  }

  .td__transformed-y {
    transform: translateY(-50%);
  }
}

.align-right {
  text-align: right;
}

.container {
  padding: 40px;
  border-radius: 40px;
  background-color: lightgray;  
  margin-bottom: 40px;
}

figcaption {
  margin-bottom: 40px;
  font-size: 24px;
  color: blue;
  font-weight: bold;
}

.link {
  display: flex;
  grid-gap: 15px;
  font-size: 16px;
  color: blue;

  img {
    width: 50px;
  }

  a {
    position: relative;
    padding: 10px 0 10px 15px;
    text-decoration: none;
    color: blue;

    &::before {
      content: "";
      position: absolute;
      left: 0;
      top: 12px;
      background-image: url("../img/icon-circle.svg");
      background-size: 100%;
      display: block;
      width: 13px;
      height: 13px;
    }
  }

  p {
    margin: 0;
    padding: 10px 0;
  }
} 

.img-half-aria {
  width: 100%;
  display: grid;
  grid-template-columns: 50% 50%;
  grid-gap: 20px;

  .img-half-aria__option {
    outline: 5px solid black;

    &--1 {

      img {
        float: left;
        width: 50%;      
      }      
    }
    
    &--2 {
      display: flex;

        img {
          width: 50%;      
        }      
    }

    &--3 {
      display: grid;
      grid-template-columns: 50% 50%;    
      
      img {
        width: 100%;      
      }     
    }

    &--4 {
      background-image: url(../img/sward.jpg);
      background-size: 50%;
      background-repeat: no-repeat;
    }
  }
  
}


